#search-list {
    #search-error {
        margin: 2em 3em 3em 3em;
    }

    .course-list-toggler {
        float: right;
        margin-left: auto;
        margin-bottom: 4px;
    }

    .course-list {
        margin: 0;
    }

    .course-wrapper {
        border-width: 1px;
        border-style: solid;
        margin-top: 15px;
        padding-right: 15px;
    }

    .course-progressbar {
        height: 4px;
        border-left-style: solid;
        border-left-width: 8px;
    }

    .course-body {
        display: flex;
    }

    .course-degrade {
        width: 8px;
        min-height: 120px;
    }

    .course-dados {
        width: calc(100% - 52px);
        display: table-cell;
        padding: 8px 16px;
    }

    .course-codigo span {
        display: inline-block;
    }

    .course-turma {
        font-size: var(--unnamed-font-size-16);
        font-weight: var(--unnamed-font-weight-600);
        font-family: var(--unnamed-font-family-noto-sans);
        color: var(--escuro-primario);
        text-align: left;
        text-transform: var(--unnamed-text-transform-uppercase);
        opacity: 1;
        margin-right: 10px;
    }

    .course-componente {
        font-size: var(--unnamed-font-size-14);
        font-weight: var(--unnamed-font-weight-500);
        font-family: var(--unnamed-font-family-noto-sans);
        text-align: left;
        color: var(--cinza-secundario);
    }

    .course-id_diario {
        font-size: var(--unnamed-font-size-16);
        font-weight: var(--unnamed-font-weight-500);
        font-family: var(--unnamed-font-family-noto-sans);
        text-align: left;
        color: var(--cinza-secundario);
        margin-left: 10px;
    }

    .course-shortname {
        font-size: var(--unnamed-font-size-14);
        font-weight: var(--unnamed-font-weight-500);
        font-family: var(--unnamed-font-family-noto-sans);
        text-align: left;
        color: var(--cinza-secundario);
    }

    .course-curso > a {
        font-size: var(--unnamed-font-size-14);
        font-weight: var(--unnamed-font-weight-500);
        font-family: var(--unnamed-font-family-noto-sans);
        text-align: left;
        color: var(--cinza-secundario);
    }

    .course-disciplina > a {
        font-size: var(--unnamed-font-size-24);
        font-weight: var(--unnamed-font-weight-500);
        font-family: var(--unnamed-font-family-noto-sans);
        text-align: left;
        text-decoration: none;
        color: var(--escuro-primario);
    }

    .course-acoes {
        width: 44px;
        min-width: 44px;
    }

    .course-progresso {
        text-align: center;
        padding: 3px 5px 3px 5px;
        opacity: 1;
        font-size: 0.8rem;
        display: inline-block;
        margin-bottom: 5px;
        min-width: 44px;
        border: 1px solid var(--background-body);
        -webkit-border-bottom-right-radius: var(--default-border-radius);
        -webkit-border-bottom-left-radius: var(--default-border-radius);
        -moz-border-radius-bottomright: var(--default-border-radius);
        -moz-border-radius-bottomleft: var(--default-border-radius);
        border-bottom-right-radius: var(--default-border-radius);
        border-bottom-left-radius: var(--default-border-radius);
    }

    .course-acao {
        margin-bottom: 6px;
        background-color: transparent;
        border: 1px solid var(--cinza-secundario);
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: var(--default-border-radius);
        min-width: 44px;
        height: 44px;
    }

    .courses.compact .course-acao.course-visible,
    .courses.compact .course-acao.course-favourite,
    .courses.compact .course-curso {
        display: none;
    }

    .courses.compact .course-acao.dropdown {
        margin-top: 6px;
    }

    .courses.compact .course-disciplina > a {
        font-size: var(--unnamed-font-size-16);
        font-weight: var(--unnamed-font-weight-bold);
    }

    .courses.compact .course-degrade {
        min-height: unset;
    }

    .courses.compact .course-acoes {
        width: 88px;
        display: flex;
        flex-direction: row-reverse;
    }

    .courses.compact .course-progresso {
        margin-bottom: 6px;
        margin-left: 10px;
        background-color: transparent;
        border: 1px solid var(--cinza-secundario);
        align-items: center;
        justify-content: center;
        -webkit-border-bottom-right-radius: var(--default-border-radius);
        -webkit-border-bottom-left-radius: var(--default-border-radius);
        -moz-border-radius-bottomright: var(--default-border-radius);
        -moz-border-radius-bottomleft: var(--default-border-radius);
        border-bottom-right-radius: var(--default-border-radius);
        border-bottom-left-radius: var(--default-border-radius);
        width: 44px;
        height: 50px;
        color: var(--cinza-secundario);
        display: inline-flex;
    }

    .courses.compact .course-acao.course-favourite {
        margin-top: 6px;
        padding: 0 15px;
    }
}
